import React from 'react';

import "./formations.css"
import "./formation-drawing.css"
import "./formation-table.css"

// Endzone Components
import EndzoneNav from '../../../components/navigators/endzone-nav/EndzoneNav';
import EndzoneButton from '../../../components/buttons/endzone-new-button/endzone-button'
import EndzoneTextInput from '../../../components/form-inputs/text-input/endzone-text-input'
import EndzoneComboBox from '../../../components/form-inputs/endzone-comboBox/endzone-combo-box';
import logo from './images/Endzone_Shield.png'

// Material UI Components
import { Alert } from '@mui/material';
import { DataGrid } from '@mui/x-data-grid';
import IconButton from '@mui/material/IconButton';

// Material UI Icons
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import CloseIcon from '@mui/icons-material/Close';

// Drawing Library
import { Stage, Layer, Rect, Text, Circle, Group} from 'react-konva';

class FormationPage extends React.Component {
    constructor (props) {
        super(props);
        this.httpHandler = require('react-http-client');

        this.state = {
            selected_formation: "New Formation",
            x: 50,
            y: 50,
            formation_players: [],
            finalize_action: "create",
            isDraw: false,
            isDrawCanvasDisplayed: false,
            isEditFormationDisplayed: true,
            isToggleDrawDisplayed: true,
            isViewDrawDisplayed: false,
            isDiscardFormationDisplayed: true,
            isDeleteFormationDisplayed: false,
            isRedrawBtnDisplayed: false,
            isDrawBtnDisplayed: true,
            isGetPlayArtDisplayed: false,
            current_WR: 0,
            current_RB: 0,
            current_TE: 0,
            current_formation: "",
            current_image: "",
            rows: [],
            alert: {
                style: {display: "none"},
                severity: "info",
                userMessage: ""
            }
        }

        this.exportStageRef = React.createRef()

        this.columns = [
            { field: 'Formation', headerName: 'Formation', flex: 2},
            { field: 'RunningBacks', headerName: 'RB', flex: 1},
            { field: 'WideReceivers', headerName: 'WR', flex: 1},
            { field: 'TightEnds', headerName: 'TE', flex: 1}
          ];
    }

    formationSelect = (e) => {
        this.setState(
            {
                selected_formation: e.row.id,
                current_WR: e.row.WideReceivers,
                current_RB: e.row.RunningBacks,
                current_TE: e.row.TightEnds,
                current_formation: e.row.Formation,
                finalize_action: "update"
            }
        )
        if(e.row)
        {
            this.setState({
                isToggleDrawDisplayed: true,
                isViewDrawDisplayed: false,
                isEditFormationDisplayed: true,
                isDiscardFormationDisplayed: false,
                isDeleteFormationDisplayed: true,
                isGetPlayArtDisplayed: true,
                current_RB: e.row.RunningBacks,
                current_WR: e.row.WideReceivers,
                current_TE: e.row.TightEnds,
                current_formation: e.row.Formation ,
                isRedrawBtnDisplayed: true,
                isDrawBtnDisplayed: false
            })
        }
    }

    getImage = () => {
        this.httpHandler.get(`/endzone/utils/formation/image/${this.state.selected_formation}`).then(
            (response) => {
                this.setState({current_image: response, isToggleDrawDisplayed: false, isViewDrawDisplayed: true})
            },
            (request) => {this.handleError(request)}
        );
    }

    getFormations = () => {
        var rows = [];
        this.httpHandler.get("/endzone/utils/formation/get").then((response) => {
            this.setState({rows: response});
        }, (request) => {
            var response = JSON.parse(request.message)
            this.setState({
                rows: [],
                alert: {
                    style: {display: "block"},
                    severity: "error",
                    userMessage: response.data
                }
            })
        })

    }

    newFormation = () => {
        this.setState(
            {
                selected_formation: "New Formation",
                finalize_action: "create",
                isToggleDrawDisplayed: true,
                isDrawCanvasDisplayed: false,
                isViewDrawDisplayed: false,
                isDiscardFormationDisplayed: true,
                isDeleteFormationDisplayed: false,
                isRedrawBtnDisplayed: false,
                isDrawBtnDisplayed: true,
                isGetPlayArtDisplayed: false,
                current_RB: 0,
                current_TE: 0,
                current_WR: 0,
                current_formation: "",
                formation_players: []
            }
        )
    }

    deleteFormation = () => {
        if(window.confirm(`Delete the following formation: ${this.state.current_formation}`))
        {
            this.httpHandler.post("/endzone/utils/formation/delete", {
                id: this.state.selected_formation
            }).then((response) => {
                this.setState({alert: {
                    style: {display: "block"},
                    severity: "success",
                    userMessage: "Successfully deleted formation"
                }}) 
                this.getFormations()
            }, (request) => {this.handleError(request)})
        }
        this.newFormation()
    }

    finalizeFormation = () => {
        let formation_name = this.state.current_formation;
        if(String(formation_name).length <= 0) {
            this.setState({alert: {
                style: {display: "block"},
                severity: "info",
                userMessage: "Please Provide a Formation Name"
            }}) 
            return
        }
        else if(String(formation_name).length > 50) {
            this.setState({alert: {
                style: {display: "block"},
                severity: "info",
                userMessage: "Formation Name Must be Less than 50 Characters"
            }}) 
            return
        }
        let rb_count = this.state.current_RB
        let wr_count = this.state.current_WR
        let te_count  = this.state.current_TE
        
        let total_count = Number(rb_count) + Number(wr_count) + Number(te_count);
        if(total_count != 5) {
            this.setState({alert: {
                style: {display: "block"},
                severity: "info",
                userMessage: "A Formation Must Have 5 Distinct Players you have " + String(total_count)
            }}) 
            return
        }
        let image = null;
        if(this.state.isDraw){image = this.exportStageRef.current.toDataURL();}

        // If action == Update
        if (this.state.finalize_action == "update") {
            this.httpHandler.post("/endzone/utils/formation/update", {
                id: this.state.selected_formation,
                formation: formation_name,
                wideRecievers: wr_count,
                runningBacks: rb_count,
                tightEnds: te_count,
                image: image
            }).then((request) => {
                this.getFormations()
                this.setState({alert: {
                    style: {display: "block"},
                    severity: "success",
                    userMessage: "Formation Update"
                }}) 
            }, (request) => this.handleError(request))
        }
        else if (this.state.finalize_action == "create") {
            this.httpHandler.post("/endzone/utils/formation/add", {
                formation: formation_name,
                wideRecievers: wr_count,
                runningBacks: rb_count,
                tightEnds: te_count,
                image: image,
            }).then((response) => {
                this.setState({alert: {
                    style: {display: "block"},
                    severity: "success",
                    userMessage: "Formation Created"
                }}) 
                this.getFormations()
                this.newFormation()

            }, (request) => this.handleError(request))
        }
    }

    componentDidMount = () => {
        this.setState({
            x: document.getElementById("drawing-area").clientWidth,
            y: document.getElementById("drawing-area").clientHeight,
            formation_players: []
        })
        this.getFormations()
    }

    reDraw = () => {
        if(window.confirm("Do you want to delete the existing formation art?"))
        {
            this.toggleDraw()
            this.state.isDraw = true
        }
    }

    toggleDraw = () => {
        this.setState({isDrawBtnDisplayed: false, isDrawCanvasDisplayed: true, isDraw: true})
    }

    cancelDraw = () => {
        this.setState({isToggleDrawDisplayed: true, isDrawCanvasDisplayed: false})
        this.state.isDraw = false
        this.discardFormation()
    }

    discardFormation = () => {
        this.setState(
            {
                formation_players: [],
                isToggleDrawDisplayed: false,
                isDrawCanvasDisplayed: false, 
                isViewDrawDisplayed: false, 
                isDrawCanvasDisplayed: false,
                current_RB: 0,
                current_TE: 0,
                current_WR: 0,
                current_formation: ""
            })
    }

    updatePlayersToPosition(position, count){
        let updated_positions = this.state.formation_players.filter(player => player !== position);
        for(let i = 0; i < count; i ++) {updated_positions.push(position)}
        this.setState({formation_players: updated_positions})
    }

    closeAlert = () => {
        let alert = {
            style: {display: "none"},
            severity: "info",
            userMessage: "" 
        }
        this.setState({alert: alert})
    }

    handleError = (request) => {
        try {
            var response = JSON.parse(request.message)
            this.setState({alert: {
                style: {display: "block"},
                severity: "error",
                userMessage: response.data
            }})
        }
        catch {
            this.setState({alert: {
                style: {display: "block"},
                severity: "error",
                userMessage: "Unexpected Error - Please Contact: endzone.support@gmail.com"
            }})
        }
    }

    render() {
        return(
            <div className='formation-page'>
                <EndzoneNav links={[{title: "Return to Coaches Corner", link: "/endzone/coaches-corner/hub"},]}></EndzoneNav>
                <div className='alert-div' style = {this.state.alert.style}>
                    <Alert variant="filled" severity={this.state.alert.severity}
                            action={
                                <IconButton
                                aria-label="close"
                                color="inherit"
                                size="large"
                                onClick={() => {
                                    this.closeAlert();
                                }}
                                >
                                <CloseIcon fontSize="inherit" />
                                </IconButton>
                            }>
                            {this.state.alert.userMessage}
                    </Alert>
                </div>
                <div className='formation-body-div'>
                    <div className='formation-table-div'>
                        <div className='formation-table-header'><h4>Edit a formation by clicking on a row in the table. New formations can be added via the button below.</h4></div>
                        <DataGrid onRowClick={(e) => this.formationSelect(e)} rows={this.state.rows} columns={this.columns} className="table" ></DataGrid>
                        <div className='formation-table-footer'><EndzoneButton text = "Design Formation" onClick = {this.newFormation} icon = {<EditIcon sx = {{"font-size": '100%', 'paddingRight': '1vh'}} ></EditIcon>}></EndzoneButton></div>
                    </div>
                    <div className='formation-body'>
                        <div className='edit-formation' style={{display: this.state.isEditFormationDisplayed ? 'inline-block' : 'none'}}>
                            <div className='edit-options'>
                                <EndzoneTextInput label = "Formation Name:" id = "formation-input" changeCallback = {(e) => this.setState({current_formation: e.target.value})} value = {this.state.current_formation}></EndzoneTextInput>
                                <EndzoneComboBox changeCallback = {(e) => {this.setState({current_RB: parseInt(e.target.value)}); this.updatePlayersToPosition("RB", parseInt(e.target.value))}} value = {this.state.current_RB} id = "rb-input" label = "RB Count:" options = {[{"text": "0", "value": "0"}, {"text": "1", "value": "1"}, {"text": "2", "value": "2"}, {"text": "3", "value": "3"}, {"text": "4", "value": "4"}, {"text": "5", "value": "5"}]}></EndzoneComboBox>
                                <EndzoneComboBox changeCallback = {(e) => {this.setState({current_WR: parseInt(e.target.value)}); this.updatePlayersToPosition("WR", parseInt(e.target.value))}} value = {this.state.current_WR} id = "wr-input" label = "WR Count:" options = {[{"text": "0", "value": "0"}, {"text": "1", "value": "1"}, {"text": "2", "value": "2"}, {"text": "3", "value": "3"}, {"text": "4", "value": "4"}, {"text": "5", "value": "5"}]}></EndzoneComboBox>
                                <EndzoneComboBox changeCallback = {(e) => {this.setState({current_TE: parseInt(e.target.value)}); this.updatePlayersToPosition("TE", parseInt(e.target.value))}} value = {this.state.current_TE} id = "te-input" label = "TE Count:" options = {[{"text": "0", "value": "0"}, {"text": "1", "value": "1"}, {"text": "2", "value": "2"}, {"text": "3", "value": "3"}, {"text": "4", "value": "4"}, {"text": "5", "value": "5"}]}></EndzoneComboBox>
                                <EndzoneButton onClick = {this.finalizeFormation} text = "Add" style = {{"padding": "5px", "margin": "0vh"}} icon = {<AddIcon sx = {{"font-size": '100%'}} ></AddIcon>} ></EndzoneButton>
                            </div>
                        </div>

                        <div className='toggle-draw' id = "toggle-draw" style={{display: this.state.isToggleDrawDisplayed ? 'block' : 'none'}}>
                            <div className='flex-btn-div'>
                                <div className='btn-div' style={{display: this.state.isDrawBtnDisplayed ? 'inline-flex' : 'none'}}><button onClick={this.toggleDraw}>Draw Formation</button></div>
                                <div className='btn-div' style={{display: this.state.isRedrawBtnDisplayed ? 'inline-flex' : 'none'}}><button onClick={this.reDraw}>Re-Draw Formation</button></div>
                                <div className='btn-div' style={{display: this.state.isGetPlayArtDisplayed ? 'inline-flex' : 'none'}}><button onClick={this.getImage}>View Existing Formation Art</button></div>
                                <div className='btn-div' ><button onClick={this.discardFormation} style={{display: this.state.isDiscardFormationDisplayed ? 'inline-flex' : 'none'}}>Discard Formation</button></div>
                                <div className='btn-div' ><button onClick={this.cancelDraw} style={{display: this.state.isDrawCanvasDisplayed ? 'inline-flex' : 'none'}}>Cancel Drawing</button></div>
                                <div className='btn-div' id = "delete-formation" style = {{display: this.state.isDeleteFormationDisplayed ? 'inline-flex': 'none'}}><button onClick={this.deleteFormation}>Delete Formation</button></div>
                            </div>
                        </div>
                        <div className='logo-div' style={{display: this.state.isDrawCanvasDisplayed || this.state.isToggleDrawDisplayed || this.state.isViewDrawDisplayed ? 'none' : 'inline-flex'}}>
                            <img src={logo} className = "load-logo"></img>
                        </div>
                        <div className='draw-div' style={{display: this.state.isDrawCanvasDisplayed ? 'block' : 'none'}}>
                            <div className='draw-formation'>
                                <div id = "drawing-area" className='drawing-area'>
                                    <Stage width={700} height={700} ref = {this.exportStageRef}>                                    
                                        <Layer id="draw-space">
                                            {this.state.formation_players.map((item, index) => {
                                                if(item == "RB")
                                                {
                                                    return (
                                                    <Group draggable={true} x={(700 / 2)} y = {(700 / 2)}>
                                                        <Rect width={33} height={33} fill="red"/>
                                                        <Text text = "RB" fontSize = {16.5} fontFamily = {"Audiowide"} fill="white" offsetX={-2.5} offsetY={-9}/>
                                                    </Group>
                                                    )
                                                }
                                                else if (item == "WR")
                                                {
                                                    return (
                                                        <Group draggable={true} x={(700 / 2)} y = {(700 / 2)}>
                                                            <Rect width={33} height={33} fill="blue"/>
                                                            <Text text = "WR" fontSize = {16.5} fontFamily = {"Audiowide"} fill="white" offsetX={-2.5} offsetY={-9}/>
                                                        </Group>
                                                    )
                                                }
                                                else if (item == "TE")
                                                {
                                                    return (
                                                        <Group draggable={true} x={(700 / 2)} y = {(700 / 2)}>
                                                            <Rect width={33} height={33} fill="green"/>
                                                            <Text text = "TE" fontSize = {16.5} fontFamily = {"Audiowide"} fill="white" offsetX={-2.5} offsetY={-9}/>
                                                        </Group>
                                                    )
                                                }
                                            })}
                                            <Group draggable={true} x={(700 / 2 - 16.5)} y = {(700 / 2) - 33}>
                                                <Rect width={33} height={33} fill="teal"/>
                                                <Text text = "QB" fontSize = {16.5} fontFamily = {"Audiowide"} fill="white" offsetX={-2.5} offsetY={-9}/>
                                            </Group>
                                            <Circle width={33} height={33} x={(700 / 2)} y = {(700 / 2) * .825} fill="black" draggable={false}/>
                                            <Rect width={33} height={33} fill="black" x={(700 / 2) + 33} y = {(700 / 2) * .775} draggable={true}/>
                                            <Rect width={33} height={33} fill="black" x={(700 / 2) - 66} y = {(700 / 2) * .775} draggable={true}/>
                                            <Rect width={33} height={33} fill="black" x={(700 / 2) + 79}y = {(700 / 2) * .775} draggable={true}/>
                                            <Rect width={33} height={33} fill="black" x={(700 / 2) - 114} y = {(700 / 2) * .775} draggable={true}/>
                                        </Layer>
                                    </Stage>
                                </div>
                            </div>
                            <div id = "drawing-toolbar" className='drawing-toolbar'>
                                <div className='toolbar-action-div'>
                                    <h2>RB</h2>
                                    <div className='toolbar-actions'>
                                        <button onClick={() => {this.setState({formation_players: [...this.state.formation_players, "RB"], current_RB: this.state.current_RB + 1})                               }
                                        }>+</button>
                                        <button onClick={() => {
                                            this.state.formation_players.splice(this.state.formation_players.lastIndexOf("RB"), 1)
                                            this.setState({current_RB: this.state.current_RB - 1, })
                                        }}>-</button>                  
                                    </div>
                                </div>
                                <div className='toolbar-action-div'>
                                    <h2>WR</h2>
                                    <div className='toolbar-actions'>
                                        <button onClick={() => {this.setState({formation_players: [...this.state.formation_players, "WR"], current_WR: this.state.current_WR + 1})  }
                                        }>+</button>
                                        <button onClick={() => {
                                            this.state.formation_players.splice(this.state.formation_players.lastIndexOf("WR"), 1)
                                            this.setState({current_WR: this.state.current_WR - 1, }
                                            )
                                        }}>-</button>                    
                                    </div>
                                </div>
                                <div className='toolbar-action-div'>
                                    <h2>TE</h2>
                                    <div className='toolbar-actions'>
                                    <button onClick={() => {this.setState({formation_players: [...this.state.formation_players, "TE"], current_TE: this.state.current_TE + 1})                               }
                                        }>+</button>
                                        <button onClick={() => {
                                            this.state.formation_players.splice(this.state.formation_players.lastIndexOf("TE"), 1)
                                            this.setState({current_TE: this.state.current_TE - 1,}
                                            )
                                        }}>-</button>                  
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className='view-draw-div' style={{display: this.state.isViewDrawDisplayed ? 'inline-flex' : 'none'}}>
                            <img src = {this.state.current_image}></img>
                        </div>
                    </div>
                </div>

            </div>
        );
    }
}

export default FormationPage;